.MenuButtonOUT{
  height: 48px;
  width: 48px;
  border-radius: 50%;
  display: inline-block;
  border: 1px solid green;
  margin-top: 0px;
  margin-left: 1vw;
  margin-right: 1vw;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.MenuButtonLEFTSIDE{
  border: 2px solid red;
  height: 100vh;
  width: 2vw;
  margin-top: 2.5vh;
  margin-left: -.5vw;
  position: -webkit-sticky;
  position: sticky;
  top: 0; 
}
.MenuButtonRIGHTSIDE{
  border: 2px solid red;
  height: 100vh;
  width: 2vw;
  margin-top: 2.5vh;
  margin-left: 34vw;
  position: -webkit-sticky;
  position: sticky;
  top: 0; 
}

.MenuButtonLEFT{
  border: 2px solid red;
  height: 2.5vh;
  width: 45vw;
  display: inline-block;
  margin-top: 0px;
  margin-right: 2vw;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.MenuButtonRIGHT{
  border: 2px solid red;
  height: 2.5vh;
  width: 42.5vw;
  display: inline-block;
  margin-top: 0px;
  margin-left: 8.5vw;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

